
function wikiService($rootScope, $http) {

    var data;
    var code;
    var message;
    var service = {
        getWikiInfo : getWikiInfo,
        addWiki : addWiki,
        updateWiki : updateWiki,
        getHistoryList : getHistoryList,
        getHistoryCount : getHistoryCount,
        getVersionInfo : getVersionInfo,
        getWikiList : getWikiList,
        getWikiIndex : getWikiIndex,
        uploadFile : uploadFile,
        getFileList : getFileList,
        deleteWiki : deleteWiki,
        getPreViewInfo : getPreViewInfo,
        update : update,
        wikiDiff : wikiDiff
    };

    return service;

    function getWikiInfo(data, shortcut) {

        return $http({
            method: 'post',
            url: '/api/wiki/getInfo',
            data: $.param({
                pKey: data,
                shortcut: shortcut
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function update(pKey, shortcut, field, data) {
        return $http({
            method: 'post',
            url: '/api/wiki/update',
            data: $.param({
                pKey: pKey,
                shortcut: shortcut,
                data: data,
                field: field
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function getHistoryList(pKey, shortcut, page, perpage) {

        return $http({
            method: 'post',
            url: '/api/wiki/getHistoryList',
            data: $.param({
                pKey: pKey,
                shortcut: shortcut,
                page: page,
                perpage: perpage,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function getHistoryCount(pKey, shortcut) {
        return $http({
            method: 'post',
            data: $.param({
                pKey: pKey,
                shortcut: shortcut,
            }),
            url: '/api/wiki/getHistoryCount',
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function getVersionInfo(pKey, shortcut, version) {

        return $http({
            method: 'post',
            url: '/api/wiki/getVersionInfo',
            data: $.param({
                pKey: pKey,
                version: version,
                shortcut: shortcut
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function addWiki(data) {
         return $http.post('/api/wiki/add',
             data,
             {
                 headers : {'Content-Type': 'application/x-www-form-urlencoded'}
             })
             .then(function(data){
                 this.data = data.data;
                 return this.data;
             });
     }

    function updateWiki(data) {
         return $http.post('/api/wiki/updateWiki',
             data,
             {
                 headers : {'Content-Type': 'application/x-www-form-urlencoded'}
             })
             .then(function(data){
                 this.data = data.data;
                 return this.data;
             });
     }

    function getWikiList(pKey, shortcut) {

        return $http({
            method: 'post',
            url: '/api/wiki/getWikiList',
            data: $.param({
                pKey: pKey,
                shortcut: shortcut
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function getWikiIndex(pKey) {

        return $http({
            method: 'post',
            url: '/api/wiki/getWikiIndex',
            data: $.param({
                pKey: pKey
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function deleteWiki(pKey, shortcut) {
        return $http({
            method: 'post',
            url: '/api/wiki/deleteWiki',
            data: $.param({
                pKey: pKey,
                shortcut: shortcut,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function uploadFile(pKey, shortcut, targetName, name) {
        return $http({
            method: 'post',
            url: '/api/wiki/uploadFileFinish',
            data: $.param({
                fileKey: targetName,
                fileName: name,
                pKey: pKey,
                shortcut: shortcut,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data) {
            this.data = data.data;
            return this.data;
        });
    }

    function getFileList(pKey, shortcut) {
        return $http({
            method: 'post',
            url: '/api/wiki/getFileList',
            data: $.param({
                pKey: pKey,
                shortcut: shortcut,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        });
    }

    function getPreViewInfo(data, pKey) {

        return $http({
            method: 'post',
            url: '/api/wiki/getPreViewInfo',
            data: $.param({
                content: data,
                pKey: pKey,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function wikiDiff(data) {
        return $http({
            method: 'post',
            data: data,
            url: '/api/wiki/wikiDiff',
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data.data;
            return this.data;
        })
    }

};
/**
 * Pass function into module
 */
angular
    .module('bugs')
    .factory('wikiService', wikiService)
