
function versionService($rootScope, $http, cacheService, toastr) {

    var data;
    var code;
    var message;
    var service = {
        getList : getList,
        create : create,
        update : update,
        view : view,
        updateVersion : updateVersion,
        getProjectInfo : getProjectInfo,
        updateOrder : updateOrder,
        updateVersionStatus : updateVersionStatus,
    };

    return service;

    function getList(data) {
        var cacheAPI = '/api/version/listAll:'+data;
        if( cacheService.checkCached( cacheAPI)) {
            return cacheService.getPromise( cacheAPI);
        } else {
            return $http.post('/api/version/listAll',
                data,
                {
                    headers : {'Content-Type': 'application/x-www-form-urlencoded'}
                })
                .then(function(data){
                   this.code = data.data.code;
                   this.data = data.data.data;
                   this.message = data.data.message;
                   if(this.code == 0){
                      cacheService.setCache(cacheAPI, this.data);
                      return this.data;
                   }else{
                       toastr.warning(this.message);
                       return false;
                   }
            })
        }
    }

    function create(data) {
        cacheService.clearCache();
        return $http.post('/api/version/add',
            data,
            {
                headers : {'Content-Type': 'application/x-www-form-urlencoded'}
            })
            .then(function(data){
                this.data = data.data;
                return this.data;
            });
    }

    function updateVersion(data) {
        cacheService.clearCache();
        return $http.post('/api/version/updateVersion',
            data,
            {
                headers : {'Content-Type': 'application/x-www-form-urlencoded'}
            })
            .then(function(data){
                this.data = data.data;
                return this.data;
            });
    }

    function view(pKey, vKey) {
        return $http({
            method: 'post',
            url: '/api/version/view',
            data: $.param({
                pKey: pKey,
                vKey: vKey
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data.data;
            return this.data;
        })
    }

    function getProjectInfo(pKey) {
        return $http({
            method: 'post',
            url: '/api/version/getProjectInfo',
            data: $.param({
                pKey: pKey
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data.data;
            return this.data;
        })
    }

    function update(pKey, vKey, field, data, arr) {
        cacheService.clearCache();
        return $http({
            method: 'post',
            url: '/api/version/update',
            data: $.param({
                pKey: pKey,
                vKey: vKey,
                data: data,
                field: field,
                inoArray : arr
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function updateOrder(order, startItem) {
        cacheService.clearCache();
        return $http({
            method: 'post',
            url: '/api/version/updateOrder',
            data: $.param({
                order : order,
                startItem : startItem,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data.data;
            return this.data;
        })
    }

    function updateVersionStatus (vKey, status, startTime, releaseTime) {
        cacheService.clearCache();
        return $http({
            method: 'post',
            url: '/api/version/updateVersionStatus',
            data: $.param({
                vKey : vKey,
                status : status,
                startTime : startTime,
                releaseTime : releaseTime,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data.data;
            return this.data;
        })
    }

};
/**
 * Pass function into module
 */
angular
    .module('bugs')
    .factory('versionService', versionService)
