function settingService($rootScope,$http){
    var data;
    var service = {
        getLoggedUserInfo : getLoggedUserInfo,
        changePassword: changePassword,
        changeEmail: changeEmail,
        getPaymentRecord: getPaymentRecord,
        updateInvoice: updateInvoice,
        loadInvoiceInfo: loadInvoiceInfo,
        changeProjectOwner: changeProjectOwner,
        loadCallingCode :loadCallingCode,
    };

    return service;

    function getLoggedUserInfo(){
        return $http.get('/api/user/setting').then(function(data){
            this.data = data.data.data;
            return this.data;
        })
    }

    function changePassword(oldPassword, newPassword) {
        return $http({
            method: 'post',
            url: '/api/user/changePassword',
            data: $.param({
                oldPassword: oldPassword,
                newPassword: newPassword,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function changeEmail(password, email) {
        return $http({
            method: 'post',
            url: '/api/user/changeEmail',
            data: $.param({
                password: password,
                email: email,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function getPaymentRecord(filterUnfinished) {
        return $http({
            method: 'post',
            url: '/api/user/getPaymentRecord',
            data: $.param({
                finished: filterUnfinished ? 1 : 0,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function updateInvoice(param) {
        return $http({
            method: 'post',
            url: '/api/user/updateInvoice',
            data: param,
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function loadInvoiceInfo(rrKey) {
        return $http({
            method: 'post',
            url: '/api/user/loadInvoiceInfo',
            data: $.param({
                rechargeKey: rrKey,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function changeProjectOwner(pKey, uKey) {
        return $http({
            method: 'post',
            url: '/api/project/changeProjectOwner',
            data: $.param({
                pKey: pKey,
                uKey: uKey,
            }),
            headers : {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }

    function loadCallingCode(){
        return $http({
            method: 'get',
            url : '/api/user/getAllCallingCode',
            headers : {'Content-Type' : 'application/x-www-form-urlencoded'}
        }).then(function(data){
            this.data = data.data;
            return this.data;
        })
    }
};



angular
    .module('bugs')
    .factory('settingService',settingService)
