function VCSService ( $http, $uibModal, $rootScope) {


    var service = {

        getRepositoryList : function (pKey, repositoryType, type) {
            return $http({
                method: 'post',
                url: '/api/VCS/getRepositoryList',
                data: $.param({
                    pKey: pKey,
                    repositoryType: repositoryType,
                    type: type ? type : 'normal'
                }),
                headers : {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function(data){
                this.data = data.data;
                return this.data;
            })
        },

        setRepository : function (pKey, repositoryInfo) {
            return $http({
                method: 'post',
                url: '/api/VCS/setRepository',
                data: $.param({
                    pKey: pKey,
                    repositoryName: repositoryInfo.repositoryName,
                    repositoryConfig: angular.toJson(repositoryInfo.configData)
                }),
                headers : {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function(data){
                this.data = data.data;
                return this.data;
            })
        },

        getRepository : function ( pKey) {
            return $http({
                method: 'post',
                url: '/api/VCS/getRepository',
                data: $.param({
                    pKey: pKey
                }),
                headers : {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function(data){
                this.data = data.data;
                return this.data;
            })
        },

        loadCommit : function ( iKey) {
            return $http({
                method: 'post',
                url: '/api/VCS/loadCommit',
                data: $.param({
                    iKey: iKey
                }),
                headers : {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function(data){
                this.data = data.data;
                return this.data;
            })
        },

        addBind : function ( iKey, bindHashes) {
            return $http({
                method: 'post',
                url: '/api/VCS/bindCommit',
                data: $.param({
                    iKey: iKey,
                    binds: bindHashes
                }),
                headers : {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function(data){
                this.data = data.data;
                return this.data;
            })
        },

        bindCommit : function ( issueInfo, callback) {

            var scope = $rootScope.$new();
            scope.data = {
                iKey : issueInfo.i_key,
                iNo: issueInfo.i_no,
                pKey: issueInfo.p_key,
                title: issueInfo.i_title,
                callback: callback
            }

            $uibModal.open({
                templateUrl: 'views/issue/bindCommit.html',
                size: 'md',
                controller: bindCommitCtrl,
                backdrop: 'static',
                scope: scope,
                keyboard: false
            });
        },

        loadCommitInfo : function (ikey) {
            return $http({
                method: 'post',
                url: '/api/VCS/loadCommitInfo',
                data: $.param({
                    iKey: ikey
                }),
                headers : {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function(data){
                this.data = data.data;
                return this.data;
            })
        },

        importIssue : function (pKey) {
            return $http({
                method: 'post',
                url: '/api/VCS/importIssue',
                data: $.param({
                    pKey: pKey
                }),
                headers : {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function(data){
                this.data = data.data;
                return this.data;
            })
        },

    }

    return service;
}


//register service
angular
    .module('bugs')
    .factory('VCSService', VCSService)
